.arrow-button{

    position: relative;
    width: 30px;
    height: 30px;
    border: 1px solid #ffffff;
    border-radius: 50%;
    background: #1A6CF1;


  &:hover {
    transition: 0.3s;
    transform: scale(1.1)
  }

  &:after {
    position: absolute;
    top: 10px;
    left: 10px;
    content: '';
    display: block;
    width: 6px;
    height: 6px;
    border-left: 3px solid #ffffff;
    border-bottom: 3px solid #ffffff;
    transform: rotate(45deg);

  }

}

.directionRight {
  transform: rotate(180deg);
  &:hover {
    transition: 0.3s;
    transform: scale(1.1) rotate(180deg);
  }
}