.chat-page-box {
  display: flex;
  height: 80vh;
  width: 1280px;
  margin: 5vh auto;
  max-width: 90%;
  border: 1px solid #999;
  border-radius: 8px;
  overflow: auto;
  background: transparent;
}

.chat-list-box {
  background: #F7F7F8;
  border-right: 1px solid #999;
  max-width: 310px;
  display: flex;
  align-content: start;
  justify-content: start;
  flex-direction: column;
  overflow: auto;

  li:nth-child(n+5):last-child .chat-room-border {
    border-bottom: none;
  }
}

.chat-area-box {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  width: 100%;
  .chat-area-footer {
    width: 100%;
    align-items: center;
  }

  .chat-area-main {
    display: flex;
    justify-content: end;
    height: 100%;
    box-sizing: border-box;
    width: 90%;
    padding: 20px;
    flex-direction: column;

    .message-date {
      margin: 10px auto;
      font-size: 12px;
      line-height: 13px;
      color: #999;
    }

  }

  .chat-msg-bottom {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    font-size: 18px;
    width: 95%;
    border-top: 1px solid #999;;

    .chat-input__msg{
      width: 100%;
      border-radius: 50px;
      border: none;
    }

    img {
      width: 32px;
      height: 32px;
    }

  }

}