.room-box{
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: space-between;
  padding: 7px 0;
  width: 100%;
  max-height: 70px;

  .room-box-content {
    display: flex;
    align-items: center;
    margin-left: 5%;

  .room-avatar{
    width: 50px;
    margin-right: 5px;
  }
  .room-chat-info{
    font-weight: bold;
    font-size: 12px;
    line-height: 18px;
    .chatName {
      font-weight: bold;
      font-size: 13px;
      color: #1E1E1E;
    }

    .userMsg {
      font-weight: 500;
      color: #1E1E1E;
    }

    .lastMsg {
      color: #999;
    }
   }
  }
  .room-adds-box {
    margin-right: 5%;
    display: flex;
    flex-direction: column;

    .dateMsg {
      margin-bottom: auto;
      align-items: start;
      font-size: 9px;
    }

    .newMsg {
        font-size: 10px;
        background: #1A6CF1;
        color: #ffffff;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .footer-chat-options {
      position: relative;
      width: 30px;
      height: 50px;
      color: #1E1E1E;
      border-radius: 50%;

      &:after {
        content: '•••';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(90deg);
        font-size: 20px;
        letter-spacing: 3px;
      }
    }
  }

}
.chat-room-border {
  content: ' ';
  border-bottom: 1px solid #999;
  width: 90%;
  margin: auto;

  }


.selected {
  background: #E4EDFD;
  opacity: 50%;
}

