 .profilePage-box {
    display: flex;
    height: 80vh;
    width: 1280px;
    margin: 5vh auto;
    max-width: 90%;
    border: 1px solid #999;
    border-radius: 8px;
    overflow: auto;
    background: #ffffff;
  }

  .box-return {
    background: #F7F7F8;
    width: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .box-profile {
    display: flex;
    justify-content: space-evenly;
    box-sizing: border-box;
    width: 90%;
    padding: 20px;
    flex-direction: column;
  }

  .box-profile-footer {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    margin: 20px 0;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    font-size: 18px;

    .box-avatar-changer {
      position: relative;
      display: flex;
      justify-content: center;
      border-radius: 50px;
      &:hover span {
        opacity: 100%;
        transition: 1s;
      }
      &:hover img {
        opacity: 80%;
        transition: 0.3s;
        transform: scale(1.1);
      }

    img {
      width: 50px;
      border: 1px solid #1A6CF1;
      border-radius: 50px;
    }
    .avatarChanger {
      display: block;
      position: absolute;
      word-wrap: break-word;
      top: 120px;
      font-size: 7px;
      width: 30px;
      text-align: center;
      transform: translate(0px, -100px);
      opacity: 0%;

    }

    }
  }

  .profile-form-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    margin: 0 auto;
    width: 60%;
  }

  .box-profile-bottom{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    margin: 3vh auto;
    width: 60%;
  }
